import * as Yup from 'yup'

export const signInFormValidation = Yup.object({
	email: Yup.string()
		.email('Ogiltigt email-format')
		.required('Detta flt r obligatoriskt.'),
	password: Yup.string()
		.min(2, 'Ange vad rendet gller. Ex: Ang. utbildning')
		.required('Detta flt r obligatoriskt.'),
})
